import {Injectable} from '@angular/core';
import * as Parse from 'parse';
import {PaginationHelper} from '../helper/pagination.helper';
import {UserStore} from '../store/user.store';
import {Router} from "@angular/router";


@Injectable({
  providedIn: 'root'
})

export class UserService {
  constructor(private paginationHelper: PaginationHelper, private userStore: UserStore, private router: Router) {
  }

  async anonymousLogin() {
    try {
      // await Parse.User.logInWith('anonymous', {authData: {
      //     id: '1'
      //   }});
      const user = await Parse.AnonymousUtils.logIn();
      console.log('User: ', user)
    } catch (e) {
      console.log('Error: ', e);
    }
  }

  async login(username, password) {
    try {
      const user = await Parse.User.logIn(username, password);
      // if (Parse.User.current().get('customer_token'))
      return '';
    } catch (e) {
      console.log(e);
      return 'Invalid email or password';
    }
  }

  async register(email, password, attributes) {
    try {
      const user = await Parse.User.signUp(email, password, attributes);
      return null;
    } catch (e) {
      console.log('Error:', e.message);
      return e.message;
    }
  }

  async logout() {
    try {
      await Parse.User.logOut();
      await this.router.navigate(['/login']);
    } catch (e) {
      console.log('Error:', e);
    }
  }

  // async changePoints(){
  //   try {
  //     await
  //   }catch (e) {
  //     console.log('Error: ', e);
  //   }
  // }

  async editUserRequest(data: any) {
    try {
      const editTable = Parse.Object.extend('Edit_User_Request');
      const edit = new editTable();
      console.log('Saving request data');
      await edit.save(data);
      console.log('Request Data saved');
    } catch (e) {
      console.log('Error: ', e);
    }
  }

  async updateUser(data: any) {
    try {
      await Parse.User.current().save(data);
    } catch (e) {
      console.log('Error', e);
    }
  }

  async saveUser() {
    try {
      await Parse.User.current().save();
    } catch (e) {
      console.log('Error: ', e);
    }
  }

  // async setUserCity(city: string) {
  //     try {
  //         await Parse.User.current().set('city', city);
  //         await Parse.User.current().save();
  //     } catch (e) {
  //         console.log('Error: ', e);
  //     }
  // }

  async resetPassword(email) {
    try {
      await Parse.User.requestPasswordReset(email);
      return true;
    } catch (e) {
      console.log('Error:', e);
      return false;
    }
  }


}
