import {Injectable} from '@angular/core';



@Injectable({
    providedIn: 'root'
})

export class PaginationHelper {
    constructor() {
    }

  static async loadData(event, service: any, store: any, limit) {
        try {
            if (store.shouldLoadMore){
                const result = await service.loadMore();
                event.target.complete();
                store.incrementPage();
                if (result.length < limit) {
                    store.setShouldLoadMore(false);
                    event.target.disabled = true;
                }
            }else{
                event.target.disabled = true;
            }
        }catch (e) {
            console.log('Error:', e);
        }
    }
  static async checkExisting(newEl, arr){
        try {
            const resultArray = arr.filter((item) => item.objectId === newEl.objectId);
            if ( resultArray.length > 0){
                return true;
            }
            return false;

        }catch (e) {
            console.log('Error:',e);
        }
    }
}

